import { Link, useParams } from "react-router-dom";
import Carousel from "../../components/Carousel";
import useMediaQuery from "../../hooks/useMediaQuery";
import { useBrandsBasedOnProductType } from "../../services/brandService";

const BrandList = () => {
  const { productTypeId } = useParams();
  const { data: brands, isLoading } =
    useBrandsBasedOnProductType(productTypeId);
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxLg = useMediaQuery("(max-width: 1024px)");

  return (
    !isLoading &&
    brands?.length > 0 && (
      <div className="container max-w-full space-y-6 max-lg:space-y-4 relative py-4">
        <div className="max-w-max mx-auto">
          <Carousel
            slideWidth={maxLg ? 80 : maxXl ? 100 : 120}
            spaceBetween={maxLg ? 10 : 30}
            allowTouchMove
          >
            {brands.map((subcategory) => (
              <Link
                key={subcategory._id}
                to={`/brand/${subcategory._id}`}
                className="space-y-2 inline-block relative"
              >
                <img
                  src={subcategory.logoUrl}
                  alt={subcategory.name || subcategory._id}
                  loading="lazy"
                  className="size-[120px] max-xl:size-[100px] max-lg:size-[80px] p-2 rounded-lg object-cover object-center bg-lavender"
                />
              </Link>
            ))}
          </Carousel>
        </div>
      </div>
    )
  );
};

export default BrandList;
