import { useState } from "react";
import { useSidebar } from "../contexts/sidebarContext";
import { FaChevronLeft } from "react-icons/fa";
import CheckBox from "./CheckBox";
import RadioInput from "./RadioInput";
import Button from "./Button";

const MobileFilter = ({
  filters,
  filterConfig,
  onFilterChange,
  onClearFilters,
}) => {
  const { openSidebar } = useSidebar();

  return (
    <div className="w-full flex items-center gap-4">
      {/* Button for "Sort By" Sidebar */}
      <FilterButton
        title="Sort By"
        icon={<FaChevronLeft />}
        onClick={() =>
          openSidebar(
            <SortBySidebar
              filters={filters}
              filterConfig={filterConfig.filter(({ key }) => key === "sortBy")}
              onFilterChange={onFilterChange}
            />
          )
        }
      />
      {/* Button for "Filter" Sidebar */}
      <FilterButton
        title="Filter"
        icon={<FaChevronLeft />}
        onClick={() =>
          openSidebar(
            <FilterSidebar
              filters={filters}
              filterConfig={filterConfig.filter(({ key }) => key !== "sortBy")}
              onFilterChange={onFilterChange}
              onClearFilters={onClearFilters}
            />
          )
        }
      />
    </div>
  );
};

const FilterButton = ({ title, icon, onClick }) => (
  <button
    type="button"
    className="flex items-center justify-center gap-2 border border-primary rounded-full px-2 py-1"
    onClick={onClick}
  >
    <p className="text-sm">{title}</p>
    <span className="text-sm">{icon}</span>
  </button>
);

const SortBySidebar = ({ filters, filterConfig, onFilterChange }) => {
  const { closeSidebar } = useSidebar();
  const sortByConfig = filterConfig.find((config) => config.key === "sortBy");

  const handleRadioChange = (filterKey, value) => {
    onFilterChange(filterKey, value);
  };

  return (
    <div className="bg-white h-svh">
      <div className="HEADING px-4 py-2 border-b border-primary flex items-center gap-2">
        <FaChevronLeft onClick={closeSidebar} />
        <p>Sort By</p>
      </div>
      <div className="p-4">
        <form className="space-y-2">
          {sortByConfig?.options.map((option) => (
            <RadioInput
              key={option.value}
              label={option.label}
              name={sortByConfig.key}
              value={option.value}
              checked={filters[sortByConfig.key] === option.value}
              onChange={() => handleRadioChange(sortByConfig.key, option.value)}
              labelClassName="text-sm capitalize"
            />
          ))}
        </form>
      </div>
    </div>
  );
};

const FilterSidebar = ({
  filters,
  filterConfig,
  onFilterChange,
  onClearFilters,
}) => {
  const { closeSidebar } = useSidebar();
  const [activeTab, setActiveTab] = useState(filterConfig[0]?.header);

  // Define the function to handle checkbox changes
  const handleCheckboxChange = (filterKey, value) => {
    const currentValues = filters[filterKey]?.split(",").filter(Boolean) || [];
    const newValues = currentValues.includes(value)
      ? currentValues.filter((v) => v !== value) // Remove if already selected
      : [...currentValues, value]; // Add if not selected

    onFilterChange(filterKey, newValues.join(","));
  };

  const handleRadioChange = (filterKey, value) => {
    onFilterChange(filterKey, value);
  };

  const renderFilterOptions = (config) => {
    return (
      <div className="p-4">
        <form className="space-y-2">
          {config.type === "radio" &&
            config.options.map((option) => (
              <RadioInput
                key={option.value}
                label={option.label}
                name={config.key}
                value={option.value}
                checked={filters[config.key] === option.value}
                onChange={() => handleRadioChange(config.key, option.value)}
                labelClassName="text-sm capitalize"
              />
            ))}
          {config.type === "checkbox" &&
            config.options.map((option) => (
              <CheckBox
                key={option.value}
                label={option.label}
                name={config.key}
                value={option.value}
                checked={filters[config.key]?.split(",").includes(option.value)}
                onChange={() => handleCheckboxChange(config.key, option.value)}
                labelClassName="text-sm"
              />
            ))}
        </form>
      </div>
    );
  };

  return (
    <div className="bg-white h-svh relative">
      <div className="HEADING px-4 py-2 border-b border-primary flex items-center gap-2">
        <FaChevronLeft onClick={closeSidebar} />
        <p>Filters</p>
      </div>
      <div className="BODY h-full flex">
        <div className="LEFT w-2/5 h-full bg-lightGray">
          <ul>
            {filterConfig.map((tab) => (
              <li
                key={tab.key}
                onClick={() => setActiveTab(tab.header)}
                className={`px-4 py-2 cursor-pointer ${
                  activeTab === tab.header && "bg-white text-blue-400"
                }`}
              >
                <p className="text-sm whitespace-nowrap">{tab.header}</p>
              </li>
            ))}
          </ul>
        </div>
        <div className="RIGHT w-3/5 h-full">
          {renderFilterOptions(
            filterConfig.find((config) => config.header === activeTab)
          )}
        </div>
      </div>
      <div className="absolute bottom-0 right-0 left-0 bg-white flex justify-between p-2">
        <Button
          type="button"
          className="px-4 py-2 bg-blue-500 rounded hover:bg-blue-600"
          onClick={onClearFilters}
          disabled={
            !filterConfig.some(
              (config) => filters[config.key] && filters[config.key] !== ""
            )
          }
        >
          Clear Filters
        </Button>
      </div>
    </div>
  );
};

export default MobileFilter;
