import { AccordionProvider } from "../contexts/accordionContext";
import { AccordionHeader, AccordionItem, AccordionPanel } from "./Accordion";
import CheckBox from "./CheckBox";
import RadioInput from "./RadioInput";
import Button from "./Button";

const Filter = ({ filters, filterConfig, onFilterChange, onClearFilters }) => {
  const handleCheckboxChange = (filterKey, value) => {
    const currentValues = filters[filterKey].split(",").filter(Boolean);
    const newValues = currentValues.includes(value)
      ? currentValues.filter((v) => v !== value)
      : [...currentValues, value];

    onFilterChange(filterKey, newValues.length > 0 ? newValues.join(",") : "");
  };

  const handleRadioChange = (filterKey, value) => {
    onFilterChange(filterKey, value);
  };

  return (
    <div className="w-70 flex-shrink-0">
      <AccordionProvider multiple defaultOpen>
        {filterConfig.map(({ type, key, options, header }) => (
          <AccordionItem key={key}>
            <AccordionHeader>{header}</AccordionHeader>
            <AccordionPanel>
              <form className="space-y-1">
                {type === "radio" &&
                  options.map((option) => (
                    <RadioInput
                      key={option.value}
                      label={option.label}
                      name={key}
                      value={option.value}
                      checked={filters[key] === option.value}
                      onChange={(e) => handleRadioChange(key, e.target.value)}
                      labelClassName="capitalize text-sm"
                    />
                  ))}

                {type === "checkbox" &&
                  options.map((option) => (
                    <CheckBox
                      key={option.value}
                      label={option.label}
                      name={key}
                      value={option.value}
                      checked={filters[key]?.split(",").includes(option.value)}
                      onChange={() => handleCheckboxChange(key, option.value)}
                      labelClassName="text-sm"
                    />
                  ))}
              </form>
            </AccordionPanel>
          </AccordionItem>
        ))}
      </AccordionProvider>

      {filters.isActive && (
        <Button
          type="button"
          className="px-4 py-2 bg-blue-500 rounded hover:bg-blue-600"
          onClick={onClearFilters}
        >
          Clear Filters
        </Button>
      )}
    </div>
  );
};

export default Filter;
